*{ // * - это мы обращаемся ко всем элементам на сайте
    box-sizing: border-box; // что бы нам было удобнее верстать что бы не верстать падинги отдельные от ширины и так просто проще
}

.wibro:hover{
    animation: 0.09s tremor ease-out infinite; 
}

@keyframes tremor {
    0%, 25% {
        left: -1px;
        transform: translateX(-0.01%);
    }
    50%, 100% {
        left: 1px;
        transform: translateX(0.01%);
    }
}

.pulse{
    animation: pulse 2s infinite;
}

