    height: 86%;
}

.hero_content-left,
.hero_content-right{
    float: left;
    height: 100%;
}

.hero_content-left{
    width: 51%;
    position: relative;
}

.hero_content-pic{
    width: 100%;
    max-width: 450px;
    height: max-content;
    position: absolute;
    top: 20%;
}

.content_left-img-burger{
    position: absolute;
    margin-left: 15px;
    margin-top: -70px;
}

.content_left-svg-drops{
    width: 100%;
    position: absolute;
}

