    width: 49%;
    position: relative;

    .hero_content-block{
        width: 100%;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
}

.hero_content-right-text{
    font-family: 'lifehack';
    font-size: 65px;
    color: #ffff;
}

.down_arrow{
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
}

.down_arrow-pic{
    width: 33px;
    height: 16px;
    fill: #fff;
    fill-opacity: .9;
    transition: all 0.2s ease-in-out;

    &:hover{
    // width: 40px;
    // height: 20px;
    fill-opacity: 1;
    transform: scale(1.5);
    }

    &:active{
        // width: 33px;
        // height: 16px;
        fill-opacity: 1;
        transform: scale(0.5);
    }
}

