.team_bottom-batton{
    height: 16px;
    width: max-content;
    display: block;
    position: relative;
    text-decoration: none;
    font-family: 'gagalin';
    color: #ffffff;
    margin-bottom: 10px;
    transition: all 0.2s ease-in-out;

    &:hover{
        color: #e7b247;
        font-size: 16px;
        text-shadow: black -2px 2px 2px;
    }

    &:active{
        font-size: 15px;
        text-shadow: black -1px 1px 2px;
    }

    &:before{
        content: "";
        position: absolute;
        display: block;
        left: -19px;
        top: 8px;
        width: 0;
        height: 0;
        border-top: 7px solid #e7b247;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
        border-bottom: 0px solid transparent;
        transform: rotate(0deg);
        transition: all 0.3s ease-in-out;
    }
}

.team_bottom-block{
    width: 100%;
    height: 0px;
    overflow: hidden;
    transition: all 0.3s ease-in-out;

}

.team_active{
    .team_bottom-batton{
        color: #e7b247;
        font-size: 15.5px;
        text-shadow: black -.5px .5px 2px;

        &:hover{
            color: #e7b247;
            font-size: 16px;
            text-shadow: black -2px 2px 2px;
        }

        &:active{
            font-size: 15px;
            text-shadow: black -1px 1px 2px;
        }

        &:before{
            transform: rotate(-180deg);
        }
    }

    .team_bottom-block{
        height: 115px;
    }
}

