}

.footer_adress{
    color: #fff;
    font-weight: 300;
    margin-bottom: 5px;
}

.footer_phone-wrap{
    width: max-content;
    height: max-content;
    transition: all 0.1s ease-in-out;

    &:hover{
        transform: scale(1.2);
    }

    &:active{
        transform: scale(1.1);
    }
}

.footer_phone-button{
    text-decoration: none;
}

.footer_phone-pic{
    vertical-align: middle;
    fill: #e45028;
