    line-height: 2rem;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-left: 20px;
    vertical-align: middle;
    transition: all 0.2s ease-in-out;
    fill: #fff;
    fill-opacity: .3;

    &:hover{
        fill-opacity: 1;
    }
}
.fixed_menu{
    position: fixed;
    right: 55px;
    top: 90px;
}

.fixed_menu-block{
    display: block;
    width: 17px;
    height: 17px;
    margin: 7px;
    position: relative;

    // width: 17px;
    // height: 17px;
    // margin-bottom: 5px;
}

.fixed_menu-item{
    display: block;
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    text-decoration: none;
    border-radius: 50%;
    transition: ease-in-out 0.1s all;

    // width: 17px;
    // height: 17px;
    // // left: 50%;
    // left: 0;
    // top: 0;
    // border-radius: 50%;
    // position: relative;
    // display: block;
    // text-decoration: none;
    // transition: .2s;

    &:hover{
        // border: 1.5px solid #fff;
        box-shadow: #fff 0px 0px 5px 1px;
    }

    &:hover .fixed_menu-link{
        width: 11px;
        height: 11px;
        margin: -6px 0 0 -6px;

        // width: 9px;
        // height: 9px;
        // top: 3px;
        // left: 3px;
    }

    &:active{
        box-shadow: #fff 0px 0px 1px 1px;
    }

    &:active .fixed_menu-link{
        width: 3px;
        height: 3px;
        margin: -2px 0 0 -2px;

        // width: 5px;
        // height: 5px;
        // top: 5px;
        // left: 5px;
    }

    &:hover .fixed_menu-title{
        // height: 16px;
        width: 75px;
        text-shadow: black 0px 0px 6px;
    }
}

.fixed_menu-title{
    position: absolute;
    width: max-content;
    text-align: right;
    right: 25px;
    bottom: 0;
    color: #fff;
    // height: 0;
    width: 0px;
    overflow: hidden;
    white-space: nowrap;
    transition: ease-in-out 0.2s all;
    // display: none;
}

// .fixed_active{
//     border: 1px solid #fff;
// }

.fixed_menu-link{
    border-radius: 50%;
    position: absolute;
    z-index: 1;
    height: 7px;
    width: 7px;
    border: 0;
    background: #fff;
    left: 50%;
    top: 50%;
    margin: -4px 0 0 -4px;
    box-shadow: black 0px 0px 6px;
    transition: ease-in-out 0.1s all;

    // width: 7px;
    // height: 7px;
    // border-radius: 50%;
    // background: #fff;
    // position: absolute;
    // top: 4px;
    // left: 4px;
    // box-shadow: black 0px 0px 6px;
    // transition: .1s;
}


.active .fixed_menu-item{
    // border: 1px solid #fff;
    box-shadow: #fff 0px 0px 10px 1px;

    &:hover{
        box-shadow: #fff 0px 0px 20px 1px;
    }

    &:active{
        box-shadow: #fff 0px 0px 1px 1px;
    }
    
}

.active .fixed_menu-link{
    width: 9px;
    height: 9px;
    margin: -5px 0 0 -5px;

    &:hover{
        box-shadow: #fff 0px 0px 2px 1px;
    }
